.panel {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: calc(50% - 340px);
  left: 30px;
  width: 400px;
  height: 680px;
  background: #ffffff;
  box-shadow: 4px 4px 10px 10px rgba(0, 0, 0, 0.1);
  padding: 35px;
  border-radius: 20px;
  overflow: hidden;
}

.panel * {
  width: 100%;
  text-decoration: none;
}

.panel .content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px auto 0 auto;
  flex: 1 1;
  justify-content: flex-start;
}

.panel .copyright-wrapper {
  width: 150px;
  height: 150px;
  margin: 0 auto;
  text-align: left;
  font-size: 14px;
  line-height: 19px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  justify-content: flex-start;
  align-items: center;
  border-radius: 50%;
  bottom: -78px;
  background-color: var(--primary-color);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  border: 1px solid transparent;
}

.panel .copyright-wrapper span,
.panel .copyright-wrapper a {
  color: white;
  text-align: center;
  display: inline-block;
  line-height: 25px;
  font-size: 11px;
}

.panel .copyright-wrapper a {
  text-decoration: underline;
}

.panel .copyright-wrapper:hover {
  background: white;
  border-color: var(--primary-color);
}

.panel .copyright-wrapper:hover span,
.panel .copyright-wrapper:hover a {
  color: var(--primary-color);
}

.panel .back-icon {
  position: absolute;
  left: 48px;
  top: 36px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
}

.panel .back-icon:hover {
  color: var(--primary-color);
}

.logo {
  width: 100%;
  height: auto;
  text-align: center;
}

.logo .logo-title {
  font-size: 22px;
  font-weight: bolder;
  color: black;
  margin: 0 auto;
  text-transform: uppercase;
  font-family: 'Segoe UI Bold';
}

.logo .logo-description {
  font-size: 15px;
  font-weight: 600;
  color: black;
  margin: 0 auto;
  display: inline-block;
  margin: 0 auto;
  text-transform: uppercase;
  font-family: 'Segoe UI Regular';
}

.logo img {
  width: 80px;
  height: auto;
  background-size: cover;
}

